.formControl {
    border: 1px solid transparent;
    transition: all 0.5s;
}

.formControl.error input {
    border: 1px solid #ff005e;
}

.formControl.error span {
    transition: all 0.5s;
}

.textError {
    transition: all 0.5s;
    display: block;
    text-align: center;
}

.formControl.error span,
.textError {
    color: #ff005e;
}

.defaultInput[readonly] {
    opacity: 1;
}